/* ---------------------------
Variables
------------------------------ */

// colors
$black: #374151;
$dark: #3c434c;
$grey: #828282;
$grey2: #ABABAB;
$light: #f5f5f5;
$white: #FFFFFF;
$link: $dark;

// fonts
$font-serif: Georgia, Times, serif;
$font-sans-serif: Arial, Helvetica, sans-serif;
$font-mono: "IBM Plex Mono", monospace;
$font-weight-thin: 100;
$font-weight-light: 300;
$font-weight-regular: 400;
$font-weight-heavy: 700;
$font-lineheight: 1.4;
$font-size: 1rem;
$font-size-small: 1rem;
$font-size-large: 1.8rem;
$font-size-extralarge: 2rem;

// grid
$width: 96%;
$gutter: 4%;
$breakpoint-small: 33.75em; // 540px
$breakpoint-med: 45em; // 720px
$breakpoint-large: 60em; // 960px
$max-width: 600px;

// utilities
$border-radius: 4px;


/* ---------------------------
Layout
------------------------------ */
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 20px;
  left: 0;
  top: 0;
  background-color: $light !important;
}

.max-width {
  max-width: $max-width;
}

header, footer {
  padding: 1rem 0;
}

footer {
  margin-top: 2rem;
  
  a {
    text-decoration: none;
  }

  nav {
    margin: 2rem 0;
  }
}

nav {
  margin: 1rem 0;

  a {
    margin-right: 20px;
    text-decoration: none;
    color: $black;
  }
}


/* ---------------------------
Typography
------------------------------ */
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: $font-size !important;
}

html, body {
  font-size: $font-size;
  font-family: $font-sans-serif;
  letter-spacing: -0.0rem;
  color: $black;
  line-height: $font-lineheight;
  font-weight: $font-weight-regular;
  line-height: $font-lineheight;
}

a {

}

blockquote {
  border-left: 5px solid $dark;
  margin-top: 3rem;
  margin-bottom: 3rem;
  margin-left: 0;
  padding-left: 3rem;
  font-family: $font-sans-serif;
  text-align: left;
  font-weight: $font-weight-heavy;
}

.slim {
  font-weight: $font-weight-thin;
}

.font-light {
  font-weight: $font-weight-light;
}

.font-regular {
  font-weight: $font-weight-regular;
}

.font-heavy {
  font-weight: $font-weight-heavy;
}

.font-serif {
  font-family: $font-serif;
}

.title-large {
  font-size: 4rem;
}

.text-large {
  font-size: 1.2rem;
}

.text-small {
  font-size: 0.9rem;
}

.text-smaller {
  font-size: 0.8rem;
}

.clean-links {
  a {
    text-decoration: none;
  }
}

.text-grey, .text-grey a {
