/* ---------------------------
Form
------------------------------ */
.form-subscribe {

  input {
    padding: 20px;
    font-size: 1rem;
    margin: 0;
    border: 1px solid $grey2;
    display: block;
    border-radius: $border-radius;
    background: none;
  }

  .submit {
    border: 0;
    background-color: $black;
    color: $white;

    &:hover {
      background-color: $dark;
    }
  }
}

.btn-dark {
  background-color: $black;
  color: $white;
  padding: 0.4rem;
  text-decoration: none;
  border: 0;

  &:hover {
    color: $white;
    background-color: $dark;
  }
}

.btn-outlined {
  color: $dark;
  border: 1px solid $grey;
  padding: 6px 16px;
  border-radius: $border-radius;
  font-size: 0.9rem;

  &:hover {
    border-color: $dark;
  }
}


/* ---------------------------
Blog
------------------------------ */
h1.post-title {
  padding-top: 0;
  margin: 0;
}

.separator {
  border-top: 1px solid $light;
  padding-top: 1rem;
}

article {
  margin-bottom: 1rem;

  h1 {
    margin: 0;
    padding: 0;
    font-size: $font-size;
  }

  a {
    width: 100%;
    display: block;
    color: $black;
    text-decoration: none;
  }

  time {
    color: $grey;
    font-family: $font-sans-serif;
  }

  image {
    height: 250px;
  }

  &:hover {
    a {
      color: $grey2;
    }
  }

}
ul {
  list-style: square;
  li {padding: 0.2rem 0;}
}

.square-list {
  list-style: square;
  line-height: 2rem;
}

.post-content {
  
  p.indent-speech {
    font-style: italic;
    margin: 1.4rem 0;
    padding: 0 3rem;

    &:first-letter {
      font-weight: bold;
    }
  }

  img {
    max-width: 100%;
  }

  .img-grey-bg {
    text-align: center;
    background: $light url("/assets/bg-nature.jpg") no-repeat;
    background-size: auto;

    img {
      width: 80%;
      margin: 20px auto;
    }
  }
}

#share-buttons {
  a {
    margin-right: 10px;
  }
}

.article-links {
  margin-top: 50px;
  font-size: $font-size-small;
  color: $grey;

  &::before {
    content: "---";
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  a {
    color: $grey;
  }

}


/* ---------------------------
Page - Work
------------------------------ */
.page-work {
  h4 {
    margin: 0;
  }
}


/* ---------------------------
Page - Recommendations
------------------------------ */
.recommend-item {
  margin-bottom: 4rem;

  h4 {
    margin: .5rem 0 0;
  }
}


/* ---------------------------
Mediaquery
------------------------------ */
@media only screen and (min-device-width: 320px) and (max-device-width: 736px) {
  .col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
    width: 100% !important;
  }

  .right {
    text-align: left;
  }

  body, html {
    font-size: 20px !important;
    padding: 0 20px !important;
  }

  header, footer {
    padding: 2rem 0;
  }

  header {
    .col-4 {
      margin-left: 0 !important;
    }
  }

  nav {
    .btn-outlined {
      margin-top: 20px;
    }
  }

  h1.post-title, article h1 {
    font-size: 44px;
    line-height: 60px;
  }

  blockquote {
    padding-left: 20px;
    line-height: 44px;
  }

  .recommend-item {
    width: 100%;
  }
}